import React, { Component } from 'react'
import { defaults } from 'lodash'
import { shell } from '../../util'

export class CommandLogger extends Component {
  componentDidMount () {
    let proc = shell(this.props.cmd, this.props.options || {}, (content) => {
      this.refs.log.add(content)
    })

    this.proc = proc
  }

  componentWillUnmount () {
     this.proc && this.proc.kill()
  }

  render () {
    let props = this.props

    return (
      <log ref='log' scrollable { ...props } />
    )
  }
}

export default CommandLogger



